import { Injectable } from '@angular/core';
import { TOOLS } from '../enums/tools';
import { EditorService } from './editor.service';
import { DrawFreeTool, DrawLineTool, FillBackgroundTool, PipetteTool } from '../tools';
import { UndoService } from './undo/undo.service';
import { CanvasService } from './canvas.service';


@Injectable()
export class ToolService {
    constructor(
        private editorService: EditorService,
        private canvasService: CanvasService,
        private undoService: UndoService
    ) {}

    createTool(tool: TOOLS, backgroundRectangle: paper.Path.Rectangle) {
        switch (tool) {
            case TOOLS.DRAW_FREE:
                return new DrawFreeTool(this.canvasService, this.editorService, this.undoService);
            case TOOLS.DRAW_LINE:
                return new DrawLineTool(this.canvasService, this.editorService, this.undoService);
            case TOOLS.FILL_BACKGROUND:
                return new FillBackgroundTool(this.canvasService, backgroundRectangle, this.editorService, this.undoService);
            case TOOLS.FILL_BUCKET:
                return new DrawFreeTool(this.canvasService, this.editorService, this.undoService);
            case TOOLS.PIPETTE:
                return new PipetteTool(this.canvasService, this.editorService, this.undoService);
        }
    }
}
