import { Component, OnInit, OnDestroy, Input, OnChanges, ViewChild, ElementRef } from '@angular/core';
import * as paper from 'paper';
import { EditorService, CanvasService, CanvasSize } from '../../../services';
import { Subject } from 'rxjs';
import { takeUntil, map } from 'rxjs/operators';
import { ToolService } from '../../../services/tool.service';


@Component({
  selector: 'app-mandala-editor-canvas',
  templateUrl: './mandala-editor-canvas.component.html',
  styleUrls: ['./mandala-editor-canvas.component.scss']
})
export class MandalaEditorCanvasComponent implements OnInit, OnDestroy {
    @Input() size: CanvasSize;
    @ViewChild('drawCanvas', {static: true}) drawCanvas: ElementRef;
    @ViewChild('fillCanvas', {static: true}) fillCanvas: ElementRef;
    @ViewChild('zonesCanvas', {static: true}) zonesCanvas: ElementRef;
    destroy$ = new Subject<boolean>();
    backgroundRect: paper.Path.Rectangle;
    tool: EditorTool;

    get width() {
        return `${this.size.width}px`;
    }

    get height() {
        return `${this.size.height}px`;
    }

    constructor(
        private editorService: EditorService,
        private canvasService: CanvasService,
        private toolService: ToolService
    ) {}

    ngOnInit() {
        this.canvasService.setDrawElement(this.drawCanvas.nativeElement);
        this.canvasService.setFillElement(this.fillCanvas.nativeElement);
        this.canvasService.setZonesElement(this.zonesCanvas.nativeElement);
        this.canvasService.updateCanvasesSize(this.size);

        this.initDraw();
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }

    initDraw() {
        const canvas: any = document.getElementById('canvas-draw');
        paper.setup(canvas);

        this.addBackgroundRect('white');

        this.editorService.getTool()
            .pipe(
                takeUntil(this.destroy$),
                map(tool => this.toolService.createTool(tool, this.backgroundRect))
            ).subscribe((tool) => {
                if (this.tool) {
                    this.tool.disable();
                }

                this.tool = tool;
                tool.enable();
            });
    }

    addBackgroundRect(color) {
        const startRect = this.createPoint(0, 0);
        const rectSize = new paper.Size(this.size.width, this.size.height);
        const baseLayer = new paper.Layer();
        baseLayer.activate();
        this.backgroundRect = new paper.Path.Rectangle(startRect, rectSize);
        this.backgroundRect.fillColor = new paper.Color(color);

        const drawLayer = new paper.Layer();
        drawLayer.activate();
    }

    private createPoint(x: number, y: number): paper.Point {
        return new paper.Point(x, y);
    }
}
