import { Subject, Observable } from 'rxjs';
import { EditorService, UndoService, CanvasService } from '../services';
import { EditorToolBase } from './tool.base';
import * as Hammer from 'hammerjs';
import { takeUntil } from 'rxjs/operators';

export interface TapPoint {
    x: number;
    y: number;
}

export abstract class EditorTapToolBase extends EditorToolBase {
    protected abstract canvasService: CanvasService;
    protected abstract editorService: EditorService;
    protected abstract undoService: UndoService;

    hummerManager: HammerManager;
    color: string;

    enable() {
        super.enable();

        this.addSubscriptions();
    }

    disable() {
        super.disable();

        this.hummerManager.destroy();
    }

    addSubscriptions() {
        const tap = new Hammer.Tap();
        this.hummerManager = new Hammer.Manager(this.canvasService.getDrawElement());
        this.hummerManager.add([tap]);
        this.hummerManager.on('tap', e => {
            const point = this.canvasService.normalizePoint(e.center);
            this.tap(point);
        });

        this.editorService.getColor().pipe(takeUntil(this.clear$))
            .subscribe(color => this.color = color);
    }

    abstract tap(point: TapPoint);
}
