import { createSelector } from '@ngrx/store';

const selectLayout = (state) => state.layout;

const getSelector = (prop) => {
    return createSelector(
        selectLayout,
        (state) => state[prop]
    );
};

export const isOpenSidebar = getSelector('isOpenSidebar');
export const isEnabledLoader = getSelector('isEnabledLoader');
