import { Component, OnInit, ViewChild, ElementRef, OnDestroy, Input } from '@angular/core';
import { Observable } from 'rxjs';
import iro from '@jaames/iro';
import { EditorService, UndoService, CanvasSize } from '../../services';


@Component({
  selector: 'app-mandala-editor',
  templateUrl: './mandala-editor.component.html',
  styleUrls: ['./mandala-editor.component.scss']
})
export class MandalaEditorComponent implements OnInit, OnDestroy {
    @Input() size: CanvasSize;
    @Input() fullScreen: boolean;

    fullScreenSize: CanvasSize;
    isVisibleTools = false;

    get canvasSize() {
        return this.fullScreen || ! this.size ? this.fullScreenSize : this.size;
    }

    constructor(
        private undoService: UndoService
    ) {}

    ngOnInit() {
        this.undoService.enable();

        this.fullScreenSize = {
            width: window.innerWidth,
            height: window.innerHeight,
        };
    }

    ngOnDestroy() {
        this.undoService.disable();
    }

    toggleTools() {
        this.isVisibleTools = !this.isVisibleTools;
    }
}
