import { createAction, props } from '@ngrx/store';
import { TOOLS } from '../../enums/tools';

export const zonesCount = createAction('[Mandala Editor] Zones Count', props<{zonesCount: number}>());
export const enableDrawMirrored = createAction('[Mandala Editor] Enable Draw Mirrored');
export const disableDrawMirrored = createAction('[Mandala Editor] Disable Draw Mirrored');
export const tool = createAction('[Mandala Editor] Tool', props<{tool: TOOLS}>());
export const strokeWidth = createAction('[Mandala Editor] Stroke Width', props<{strokeWidth: number}>());
export const color = createAction('[Mandala Editor] Color', props<{color: string}>());
