import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { StoreModule } from '@ngrx/store';
import { AppCommonModule } from '../common/common.module';
import { MatButtonModule } from '@angular/material/button';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSliderModule } from '@angular/material/slider';
import { MatIconModule } from '@angular/material/icon';
import { MatTooltipModule } from '@angular/material/tooltip';

import 'paper';

import {
    MandalaEditorComponent, MandalaEditorCanvasComponent, MandalaEditorColorpickerComponent, MandalaEditorToolsComponent
} from './components';
import { MandalaDrawZonesDirective } from './directives';
import { UndoService, EditorService, ToolService, CanvasService } from './services';

import * as editor from './store/reducers/editor.reducer';


@NgModule({
    declarations: [
        MandalaEditorComponent,
        MandalaEditorCanvasComponent,
        MandalaEditorColorpickerComponent,
        MandalaEditorToolsComponent,
        MandalaDrawZonesDirective
    ],
    imports: [
        CommonModule,
        MatButtonModule,
        MatCheckboxModule,
        MatSliderModule,
        MatIconModule,
        MatTooltipModule,
        AppCommonModule,
        FormsModule,
        StoreModule.forFeature('mandala', {
            editor: editor.reducer,
        })
    ],
    exports: [
        MandalaEditorComponent
    ],
    providers: [
        UndoService,
        EditorService,
        ToolService,
        CanvasService
    ]
})
export class MandalaModule {
}
