import { EditorTapToolBase, TapPoint } from './tap.base';
import { EditorService, UndoService, CanvasService } from '../services';
import * as paper from 'paper';

export class FillBackgroundTool extends EditorTapToolBase {
    constructor(
        protected canvasService: CanvasService,
        protected backgroundRect: paper.Path.Rectangle,
        protected editorService: EditorService,
        protected undoService: UndoService
    ) {
        super();
    }

    tap(point: TapPoint) {
        const prevColor = this.backgroundRect.fillColor;
        const currentColor = this.color;
        this.updateBackgroundColor(currentColor);

        this.undoService.addCommand({
            undo: () => this.updateBackgroundColor(prevColor),
            redo: () => this.updateBackgroundColor(currentColor)
        });
    }

    private updateBackgroundColor(color) {
        this.backgroundRect.fillColor = new paper.Color(color);
    }
}
